.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.input-no-style,
.select-no-style {
  border: none;
  outline: none;
  background-color: transparent;
  text-align: center;

}
th{
  padding: 8px 10px;
}
input{
  font-size: 12pt;
  font-weight: 600;
}

select{
  padding: 8px 10px;
  font-size: 12pt;
  font-weight: 600;
}
table{
  width:1600px;
}
.result{
  background-color: rgb(0, 100, 0); color: white; font-weight: bold;
  height:30px;
}
.main-con{
  margin:20px;
}
.div-con{
  background-color: #dbdbdb;
  box-shadow: inset;
  border: 1px solid #7eb32f;
  margin-bottom: 40px;
  padding:20px;
}
.button {
  margin: 2px;
  background-color: green;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}